import _ from 'lodash'
import moment from 'moment'

import Paginate from '../../mixins/paginate'

import AgentSearch from '../../components/agent-search'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Paginate
  ],

  components: {
    AgentSearch
  },

  data() {
    return {
      list: {
        data: [],
        recordsFiltered: 0,
        totalCollections: 0
      },
      load: {
        agent_id: null,
        username: null,
        amount: 0,
        status: null
      },
      rules: {
        agent_id: [
          {
            required: true,
            message: 'The master agent/sub-agent field is required.',
            trigger: 'blur'
          },
          {
            required: true,
            message: 'The master agent/sub-agent field is required.',
            trigger: 'change'
          }
        ],

        amount: [
          {
            validator(rule, value, callback) {
              if (value === 0) {
                callback(new Error('Amount is required.'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],

        status: [
          {
            required: true,
            message: 'Status is required.',
            trigger: 'blur'
          }
        ],
      },
      filter: {
        draw: [],
        daterange: null,
        status: null
      },
      showDialog: false,
      isLoading: true,
      isSaving: false,
      route,
    }
  },

  computed: {
    dialogTitle() {
      return 'Load agent credit'
    },

    tableHeight() {
      return window.innerHeight - 250
    },

    validSend() {
      return this.load.agent_id != null
        && this.load.amount > 0
        && this.load.status
    },
  },

  async mounted() {
    await this.fetch()
  },

  methods: {
    handleClose(done) {
      this.load = {
        agent_id: null,
        username: null,
        amount: 0,
        status: null
      }

      if (typeof done == 'function') {
        done()
      }
    },

    openDialog() {
      this.showDialog = true
    },

    closeDialog() {
      this.handleClose()
      this.showDialog = false
    },

    onAgent(username) {
      this.load.username = username
    },

    async fetch() {
      this.isLoading = true

      try {
        let params = {
          start: (this.currPage - 1) * this.currPageSize,
          length: this.currPageSize,

          // Global search
          'search[value]': this.search,

          // Account #
          'columns[0][data]': 'account_no',
          'columns[0][name]': 'account_no',
          'columns[0][searchable]': true,
          'columns[0][orderable]': true,
          'columns[0][search][value]': '',
          'columns[0][search][regex]': true,

          // Player
          'columns[1][data]': 'username',
          'columns[1][name]': 'username',
          'columns[1][searchable]': true,
          'columns[1][orderable]': true,
          'columns[1][search][value]': '',
          'columns[1][search][regex]': true,

          // Contact #
          'columns[2][data]': 'mobilenumber',
          'columns[2][name]': 'mobilenumber',
          'columns[2][searchable]': true,
          'columns[2][orderable]': true,
          'columns[2][search][value]': '',
          'columns[2][search][regex]': true,

          // Top-up date #
          'columns[3][data]': 'date_created',
          'columns[3][name]': 'date_created',
          'columns[3][searchable]': true,
          'columns[3][orderable]': true,
          'columns[3][search][value]': '',
          'columns[3][search][regex]': true,

          // Credit
          'columns[4][data]': 'debit',
          'columns[4][name]': 'debit',
          'columns[4][searchable]': true,
          'columns[4][orderable]': true,
          'columns[4][search][value]': '',
          'columns[4][search][regex]': true,

          // // Status
          // 'columns[5][data]': 'status',
          // 'columns[5][name]': 'status',
          // 'columns[5][searchable]': true,
          // 'columns[5][orderable]': true,
          // 'columns[5][search][value]': '',
          // 'columns[5][search][regex]': true,
        }

        if (this.filter.daterange) {
          let dateFormat = 'MM-DD-YYYY'
          params.from = moment(this.filter.daterange[0]).format(dateFormat)
          params.to = moment(this.filter.daterange[1]).format(dateFormat)
        }

        if (this.filter.draw.length) {
          params.draw = this.filter.draw.join(',')
        }

        if (this.filter.status) {
          params.status = this.filter.status
        }

        let list = await this.$store.dispatch('agent_credits/list', params)

        this.list.totalCollections = list.totalCollections
        this.list.data = list.data.map(d => {
          d.time = new Date(d.drawdate).getTime()
          return d
        })
        this.list.recordsFiltered = list.recordsFiltered
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async removeEntry(row) {
      if (this.isLoading) return

      this.isLoading = true

      try {
        await this.$store.dispatch('agent_credits/remove', row.id)
        await this.fetch()
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async save() {
      if (this.isSaving) return

      try {
        let message = `
          You are about to load ${ this.load.amount } for
          ${ this.load.username }. Click Yes to confirm,
          otherwise Cancel.
        `
        await this.$confirm(message, 'Confirmation', {
          confirmButtonText: 'Yes',
          cancelButtonText: 'Cancel',
          type: 'warning'
        })

        try {
          await this.$store.dispatch('agent_credits/save', this.load)
          let message = 'Load was successful.'

          this.fetch()
          this.closeDialog()
          this.$message({
            type: 'success',
            message
          })
        } catch(e) {
          this.$message({
            type: 'error',
            message: e.message
          })
        }
      } catch(e) {}
    }
  }
}



